"use client";

import { useState, useEffect } from "react";
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from "@/components/ui/card";
import { Button } from "@/components/ui/button";
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from "@/components/ui/select";
import { Alert, AlertDescription } from "@/components/ui/alert";
import { Progress } from "@/components/ui/progress";
import { Upload, Download, FileText, Image, File } from "lucide-react";

interface CompressionStats {
  originalSize: number;
  compressedSize: number;
  ratio: number;
  processingTime: number;
}

export default function CompressionPortal() {
  const [file, setFile] = useState<File | null>(null);
  const [algorithm, setAlgorithm] = useState<string>("");
  const [mode, setMode] = useState<"compress" | "decompress">("compress");
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState<string>("");
  const [stats, setStats] = useState<CompressionStats | null>(null);
  const [processedFileUrl, setProcessedFileUrl] = useState<string>("");

  // Cleanup function to revoke object URL
  useEffect(() => {
    return () => {
      if (processedFileUrl) {
        URL.revokeObjectURL(processedFileUrl);
      }
    };
  }, [processedFileUrl]);

  const isValidFileType = (file: File): boolean => {
    // Handle common image MIME type variations
    if (file.type.startsWith('image/')) {
      const imageTypes = ['jpeg', 'jpg', 'png'];
      const mimeSubtype = file.type.split('/')[1].toLowerCase();
      if (imageTypes.includes(mimeSubtype)) {
        return true;
      }
    }

    // Check other valid MIME types
    const validTypes = [
      'application/pdf',
      'application/msword',
      'application/vnd.openxmlformats-officedocument.wordprocessingml.document',
      'text/plain'
    ];
    
    if (validTypes.includes(file.type)) {
      return true;
    }
    
    // Check file extension as fallback
    const extension = file.name.split('.').pop()?.toLowerCase();
    const validExtensions = ['jpg', 'jpeg', 'png', 'pdf', 'doc', 'docx', 'txt'];
    return extension ? validExtensions.includes(extension) : false;
  };

  const handleFileChange = async (e: React.ChangeEvent<HTMLInputElement>) => {
    if (e.target.files && e.target.files[0]) {
      const selectedFile = e.target.files[0];
      
      // Check file size
      if (selectedFile.size > 10 * 1024 * 1024) {
        setError("File size must be less than 10MB");
        return;
      }

      // Validate file type
      if (!isValidFileType(selectedFile)) {
        setError("Unsupported file type. Please upload an image (jpg, jpeg, png) or document (pdf, doc, docx, txt)");
        return;
      }

      setFile(selectedFile);
      setError("");
    }
  };

  const formatFileSize = (bytes: number): string => {
    if (bytes === 0) return "0 Bytes";
    const k = 1024;
    const sizes = ["Bytes", "KB", "MB", "GB"];
    const i = Math.floor(Math.log(bytes) / Math.log(k));
    return parseFloat((bytes / Math.pow(k, i)).toFixed(2)) + " " + sizes[i];
  };

  const processFile = async () => {
    if (!file || !algorithm) {
      setError("Please select a file and compression algorithm");
      return;
    }

    setLoading(true);
    setError("");

    const formData = new FormData();
    formData.append("file", file);
    formData.append("algorithm", algorithm);

    try {
      const response = await fetch(`/api/${mode}`, {
        method: "POST",
        body: formData,
      });

      if (!response.ok) {
        throw new Error(await response.text());
      }

      // Get the blob response and create a download URL
      const blob = await response.blob();
      const fileName = response.headers.get("Content-Disposition")?.split("filename=")[1]?.replace(/"/g, "") || "compressed-file";
      const downloadUrl = URL.createObjectURL(blob);
      setProcessedFileUrl(downloadUrl);
      
      // Get stats from response headers
      setStats({
        originalSize: parseInt(response.headers.get("X-Original-Size") || "0"),
        compressedSize: parseInt(response.headers.get("X-Processed-Size") || "0"),
        ratio: parseFloat(response.headers.get("X-Compression-Ratio") || "0"),
        processingTime: parseFloat(response.headers.get("X-Processing-Time") || "0"),
      });
    } catch (err) {
      setError(err instanceof Error ? err.message : "An error occurred");
    } finally {
      setLoading(false);
    }
  };

  return (
    <main className="container mx-auto px-4 py-8">
      <Card className="max-w-4xl mx-auto bg-card border-border">
        <CardHeader>
          <CardTitle className="text-3xl font-bold text-center">
            Data Compression Portal
          </CardTitle>
          <CardDescription className="text-center">
            Upload files and compress/decompress them using various algorithms
          </CardDescription>
        </CardHeader>
        <CardContent className="space-y-6">
          {/* File Upload Section */}
          <div 
            className={`border-2 border-dashed rounded-lg p-8 text-center transition-colors ${
              file ? 'border-primary bg-primary/5' : 'border-border hover:border-primary/50'
            }`}
            onDragOver={(e) => {
              e.preventDefault();
              e.stopPropagation();
              e.currentTarget.classList.add('border-primary');
            }}
            onDragLeave={(e) => {
              e.preventDefault();
              e.stopPropagation();
              e.currentTarget.classList.remove('border-primary');
            }}
            onDrop={(e) => {
              e.preventDefault();
              e.stopPropagation();
              e.currentTarget.classList.remove('border-primary');
              const file = e.dataTransfer.files[0];
              if (file) {
                if (file.size > 10 * 1024 * 1024) {
                  setError("File size must be less than 10MB");
                  return;
                }
                if (!isValidFileType(file)) {
                  setError("Unsupported file type. Please upload an image (jpg, jpeg, png) or document (pdf, doc, docx, txt)");
                  return;
                }
                setFile(file);
                setError("");
              }
            }}
          >
            <input
              type="file"
              onChange={handleFileChange}
              className="hidden"
              id="file-upload"
              accept=".txt,.jpg,.jpeg,.png,.pdf,.doc,.docx"
            />
            <label
              htmlFor="file-upload"
              className="cursor-pointer flex flex-col items-center"
            >
              {file ? (
                file.type.startsWith('image/') ? (
                  <Image className="h-12 w-12 text-primary mb-4" />
                ) : file.type === 'text/plain' ? (
                  <FileText className="h-12 w-12 text-primary mb-4" />
                ) : (
                  <File className="h-12 w-12 text-primary mb-4" />
                )
              ) : (
                <Upload className="h-12 w-12 text-muted-foreground mb-4" />
              )}
              <span className="text-sm text-muted-foreground">
                {file ? file.name : "Click to upload or drag and drop"}
              </span>
              <span className="text-xs text-muted-foreground mt-2">
                Max file size: 10MB
              </span>
              <div className="text-xs text-muted-foreground mt-2">
                Supported formats: <br />
                Images (.jpg, .jpeg, .png) <br />
                Documents (.pdf, .doc, .docx, .txt)
              </div>
            </label>
          </div>

          {/* Algorithm Selection */}
          <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
            <Select onValueChange={setAlgorithm} value={algorithm}>
              <SelectTrigger className="w-full">
                <SelectValue placeholder="Select algorithm" />
              </SelectTrigger>
              <SelectContent>
                <SelectItem value="huffman">Huffman Coding</SelectItem>
                <SelectItem value="rle">Run-Length Encoding (RLE)</SelectItem>
              </SelectContent>
            </Select>

            <div className="flex gap-2">
              <Button
                variant={mode === "compress" ? "default" : "outline"}
                onClick={() => setMode("compress")}
                className="flex-1"
              >
                Compress
              </Button>
              <Button
                variant={mode === "decompress" ? "default" : "outline"}
                onClick={() => setMode("decompress")}
                className="flex-1"
              >
                Decompress
              </Button>
            </div>
          </div>

          {/* Process Button */}
          <Button
            onClick={processFile}
            disabled={loading || !file || !algorithm}
            className="w-full"
          >
            {loading ? (
              <>
              <div className="animate-spin mr-2">â†»</div>
                Processing...
              </>
            ) : (
              "Process File"
            )}
          </Button>

          {/* Error Display */}
          {error && (
            <Alert variant="destructive">
              <AlertDescription>{error}</AlertDescription>
            </Alert>
          )}

          {/* Stats Display */}
          {stats && (
            <Card className="mt-6 border-border">
              <CardHeader>
                <CardTitle>Compression Statistics</CardTitle>
              </CardHeader>
              <CardContent className="space-y-4">
                <div className="grid grid-cols-2 gap-4">
                  <div>
                    <p className="text-sm text-muted-foreground">Original Size</p>
                    <p className="text-lg font-semibold text-foreground">
                      {formatFileSize(stats.originalSize)}
                    </p>
                  </div>
                  <div>
                    <p className="text-sm text-muted-foreground">Processed Size</p>
                    <p className="text-lg font-semibold text-foreground">
                      {formatFileSize(stats.compressedSize)}
                    </p>
                  </div>
                </div>
                <div>
                  <p className="text-sm text-muted-foreground mb-2">Compression Ratio</p>
                  <Progress value={stats.ratio * 100} />
                  <p className="text-xs text-muted-foreground mt-1">
                    {(stats.ratio * 100).toFixed(1)}% reduction
                  </p>
                </div>
                <div>
                  <p className="text-sm text-muted-foreground">Processing Time</p>
                  <p className="text-lg font-semibold text-foreground">
                    {stats.processingTime.toFixed(2)}ms
                  </p>
                </div>
              </CardContent>
            </Card>
          )}

          {/* Download Button */}
          {processedFileUrl && (
            <Button
              variant="outline"
              className="w-full"
              onClick={async () => {
                const a = document.createElement('a');
                a.href = processedFileUrl;
                
                // Keep original filename
                a.download = file?.name || 'file';
                document.body.appendChild(a);
                a.click();
                document.body.removeChild(a);
              }}
            >
              <Download className="mr-2 h-4 w-4" />
              Download Processed File
            </Button>
          )}
        </CardContent>
      </Card>

      {/* Algorithm Explanations */}
      <Card className="max-w-4xl mx-auto mt-8 bg-card border-border">
        <CardHeader>
          <CardTitle>Algorithm Explanations</CardTitle>
        </CardHeader>
        <CardContent className="space-y-4">
          <div>
            <h3 className="font-semibold mb-2">Huffman Coding</h3>
            <p className="text-sm text-muted-foreground">
              Huffman coding is a data compression technique that assigns variable-length codes to characters based on their frequency of occurrence. More frequent characters get shorter codes, resulting in overall compression of the data.
            </p>
          </div>
          <div>
            <h3 className="font-semibold mb-2">Run-Length Encoding (RLE)</h3>
            <p className="text-sm text-muted-foreground">
              RLE is a simple compression algorithm that works by replacing sequences of identical characters with a count and the character. For example, "AAAAABBB" becomes "5A3B", making it effective for data with many repeated sequences.
            </p>
          </div>
        </CardContent>
      </Card>
    </main>
  );
}
